import { AnalyticsManagerAbstract } from '@analytics/services/manager';
import { Injectable } from "@angular/core";
import { Resolve, Router } from "@angular/router";
import { IsJmart } from '../interfaces/is-jmart.interface';
import { LandingService } from "../services";

@Injectable()
export class IsJmartResolver implements Resolve<any> {
    constructor(
        private router: Router,
        private landingService: LandingService,
        private analytics: AnalyticsManagerAbstract,
    ) {}

    async resolve() {
        try {
            const isJmart: IsJmart = await this.landingService.resolveAccess();
            this.logLandingRoute(isJmart);
            return isJmart;
        } catch {
            this.router.navigate(['jmart', 'landing', 'went-wrong']);
        }
    }

    private logLandingRoute({
        isJmartCompany,
        isStoreClaimSent
    }: IsJmart): void {
        const [ { status: ProductJmartStatus } ] = [
            { status: 'notconnected', check: !isJmartCompany && !isStoreClaimSent },
            { status: 'request', check: !isJmartCompany && isStoreClaimSent },
            { status: 'connected', check: isJmartCompany },
        ].filter(({ check }) => check);
        this.analytics.logEvent('JMart_Start', { version: 1, ProductJmartStatus });
    }
}
