import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'j-failed-form',
  templateUrl: './failed-form.component.html',
  styleUrls: ['./failed-form.component.scss']
})
export class FailedFormComponent implements OnInit {

  @Output() onRepeat = new EventEmitter<string>();

  constructor() { }

  ngOnInit(): void {
  }

  repeat() {
    this.onRepeat.emit('default');
  }
}
