import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup, NgForm, Validators } from '@angular/forms';
import { CategoriesService } from '../../services/categories/categories.service';
import { IApplication, ICategory } from '../../models';
import { LandingService } from '../../services';
import { AnalyticsManagerAbstract } from '@analytics/services/manager';
import { CompanyService } from '@company/company.service';
import { Subscription } from 'rxjs';
import { skip } from 'rxjs/operators';

export type FormStatusType = 'default' | 'success' | 'failed';

@Component({
  selector: 'j-application-form',
  templateUrl: './application-form.component.html',
  styleUrls: ['./application-form.component.scss']
})

export class ApplicationFormComponent implements OnInit, OnDestroy {
  categories: ICategory[];
  categoryNames: string[];

  form: FormGroup;
  isSubmitted = false;
  formStatus: FormStatusType = 'default';

  hasError: boolean;

  private changeAuthoritySubscribtion: Subscription;

  emailPattern = '^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$';

  constructor(
    private categoriesService: CategoriesService,
    private landingService: LandingService,
    private analytics: AnalyticsManagerAbstract,
    private companyService: CompanyService,
  ) {
    this.form = new FormGroup({
      category: new FormControl(null, Validators.required),
      phone: new FormControl('', Validators.required),
      email: new FormControl('', [Validators.required, Validators.email, Validators.pattern(this.emailPattern)])
    });
    this.changeAuthoritySubscribtion = this.companyService.currentAuthoritySubject$.pipe(
      skip(1)
    ).subscribe(
      () => {
        if (this.formStatus !== 'default') {
          this.onRepeat('default');
        }
      }
    );  }

  ngOnInit(): void {
    this.getCategories();
  }

  ngOnDestroy() {
    this.changeAuthoritySubscribtion.unsubscribe();
  }

  async submit() {
    this.isSubmitted = true;

    if (this.form.invalid) {
      return;
    }

    const category = this.categories.find(el => {
      if(el.name === this.form.value.category) {
         return el;
      }
    });

    const application: IApplication = {
      category: category.id,
      phone: '+7' + this.form.value.phone,
      email: this.form.value.email
    };

    try {
      await this.landingService.createApplication(application);
      this.formStatus = 'success';
      this.logSubmitResult();
    } catch (e) {
      this.formStatus = 'failed';
      this.logSubmitResult(true);
    } finally {
      this.formReset();
    }

  }

  private logSubmitResult(error: boolean = false) {
    this.analytics.logEvent('JMart_SendRequest_Click', {
      version: 1,
      error,
    });
  }

  selectedCategory(category: string) {
    this.analytics.logEvent('JMart_Category_Click', { version: 1 });
    this.form.get('category').setValue(category);
  }

  onRepeat(value: FormStatusType) {
    this.formStatus = value;
  }

  async getCategories() {
    const categories = await this.categoriesService.getCategories();
    this.categories = categories;
    this.categoryNames = categories.map((category) => category.name);
  }

  get categoryError() {
    return this.form.get('category').errors;
  }

  get phoneError() {
    return this.form.get('phone').errors;
  }

  get emailError() {
    return this.form.get('email').errors;
  }

  formReset() {
    this.form.get('category').reset();
    this.form.get('phone').setValue('');
    this.form.get('email').reset();
    this.isSubmitted = false;
  }
}
