import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CompanyService } from '@company/company.service';
import { Subscription } from 'rxjs';
import { distinctUntilChanged, skip } from 'rxjs/operators';
import { IsJmart } from './interfaces/is-jmart.interface';
import { LandingService } from './services';

@Component({
  selector: 'j-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.scss']
})
export class LandingComponent implements OnInit, OnDestroy {

  isJmart: IsJmart;

  private changeAuthoritySubscribtion: Subscription;

  constructor(
    private route: ActivatedRoute,
    private companyService: CompanyService,
    private landingService: LandingService,
  ) { }

  ngOnDestroy(): void {
    this.changeAuthoritySubscribtion.unsubscribe();
  }

  ngOnInit(): void {
    this.isJmart = this.route.snapshot.data['isJmart'];
    this.changeAuthoritySubscribtion = this.companyService.currentAuthoritySubject$.pipe(
      skip(1)
    ).subscribe(
      async () => {
        this.isJmart = await this.landingService.resolveAccess();
      }
    );
  }


}
