import { Injectable } from '@angular/core';
import { ApiService } from '@api/api.service';
import { CompanyService } from '@company/company.service';
import { IsJmart } from '../interfaces/is-jmart.interface';
import { HasAccess } from '../interfaces/has-access.interface';
import { IApplication } from '../models';
import { AnalyticsManagerAbstract } from '@analytics/services/manager';

@Injectable()
export class LandingService implements HasAccess<IsJmart> {
  constructor(
    private apiService: ApiService,
    private companyService: CompanyService,
  ) {}

  private isJmart: IsJmart = null;

  public async resolveAccess(): Promise<IsJmart> {
    const companyId = this.companyService.currentId;
    const url = `companies/${companyId}/is-jmart`;
    const isJmartPromise = this.apiService.get<IsJmart>(url);
    this.isJmart = await isJmartPromise;
    return isJmartPromise;
  }

  hasAccess(): IsJmart {
    return this.isJmart;
  }

  createApplication(application: IApplication): Promise<IApplication> {
    const companyId = this.companyService.currentId;
    const url = `companies/${companyId}/store-claim`;
    const body = application;
    return this.apiService.post<IApplication>(url, body);
  }

}
